.Flex {
  display: flex;
  align-items: center;
  justify-content: center;
}

.hover {
  transition: 0.5s;

  &:hover {
    border: 1px solid #7d4c21;
    background-color: transparent;
    color: #7d4c21;
  }
}

@keyframes pulse {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.6;
  }

  100% {
    opacity: 1;
  }
}
