// Slider

.slider {
  position: relative;
  // width: 100%;
  // top: calc(50% + 17px);
  // left: 50%;
  // transform: translate(-50%, -50%);
  margin-bottom: 75px;

  // .swiper-container {
  //     padding-bottom: 13px;
  // }

  .swiper-pagination {
    bottom: 0;

    @include Sm {
      bottom: -40px;
      left: 15px;

      @include LTR {
        right: 15px;
        left: unset;
      }
    }

    span {
      width: 17px;
      height: 5px;
      border-radius: 3px;
      background-color: #7d4c21;
      transition: 0.5s;
      margin: 0 2.5px;

      &:focus {
        outline: none;
      }

      &.pagination-active-bullet {
        opacity: 1;
        background-color: #bf8b4a;
      }
    }
  }

  .swiper-slide {
    text-align: left;

    @include LTR {
      text-align: right;
    }

    .row {
      @include mob480 {
        margin: 0 !important;
      }
    }

    p {
      transform: translateX(15px);

      @include LTR {
        transform: translateX(-15px);
      }

      opacity: 0;
      font-size: 18px;
      color: #000;
      line-height: 35px;
      width: 450px;
      margin-bottom: 20px;
      transition: 0.7s 1.4s;

      @include mob480 {
        width: auto;
      }
    }

    h1 {
      transform: translateX(15px);

      @include LTR {
        transform: translateX(-15px);
      }

      opacity: 0;
      font-size: 30px;
      font-family: bold;
      color: #7d4c21;
      position: relative;
      margin-bottom: 30px;
      transition: 0.7s 1.2s;

      &:after {
        content: "";
        display: block;
        width: 24px;
        height: 4px;
        border-radius: 2px;
        background-color: $color-sec;
        margin-top: 10px;
      }

      @include mob480 {
        font-size: 30px;
      }
    }

    a {
      transform: translateX(15px);

      @include LTR {
        transform: translateX(-15px);
      }

      opacity: 0;
      @extend .Flex;
      width: 130px;
      height: 50px;
      background-color: $color-sec;
      color: #fff;
      border-radius: 100px;
      font-size: 18px;
      font-family: bold;
      transition: 0.7s 1.6s;
    }

    .inner-content {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    .slider-pic-animate {
      position: relative;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      filter: blur(5px);
      opacity: 0;

      img {
        border-radius: 40px;
      }

      @include Sm {
        display: none;
      }
    }

    &.swiper-slide-active {
      .pos {
        transform: none !important;
      }

      .inner-content {
        > * {
          transform: none !important;
          filter: none;
          opacity: 1;
        }

        a {
          filter: url("#goo");
        }
      }

      .slider-pic-animate {
        filter: none;
        opacity: 1;
        transition: 0.5s 0.4s;
      }
    }
  }
}

