// Products

.products {
  padding: 58px 0 71px;
  background-image: url("../images/products-bg.jpg");
  background-size: 100% 100%;
  background-repeat: no-repeat;

  @include Sm {
    background-size: cover;
  }

  .section-head {
    text-align: center;

    h2 {
      font-size: 40px;
      font-family: bold;
      color: #fff;
    }

