        border-radius: 40px;
        transition: 0.5s;
        object-fit: cover;
      }

      &:hover {
        transform: translateY(-5px);
        box-shadow: 0 7px 10px rgba(255, 255, 255, 0.2);

        img {
          transform: scale(1.12);
        }
      }
    }

    .name {
      font-size: 22px;
      text-align: center;
      color: #fff;
      margin-top: 22px;
      font-family: bold;
    }
  }
}

