      @extend .Flex;
      font-size: 18px;
      font-family: bold;
      color: #fff;
      @extend .hover;
      margin-top: 0;

      @include Xs {
        top: 7px;
        right: 0;
        left: unset;
        width: 110px;
        height: 40px;
        font-size: 15px;
      }

      &:after,
      &:before {
        background-color: #244d75;
      }
    }
  }

  iframe {
    width: 100%;
    height: 100%;
    border-radius: 40px;
  }

  .col-lg-6 {
    @include Sm {
      margin-bottom: 30px;
    }
  }

  .Occord {
    .box {
      &:not(:last-child) {
        margin-bottom: 11px;
      }

      .title {
        cursor: pointer;
        padding: 11px 20px;
        background-color: #7d4c21;
        border-radius: 30px;
        font-size: 16px;
        font-family: bold;
        color: #fff;
        transition: 0.5s;

        &.bor {
          border-radius: 30px 30px 0 0 !important;
        }
      }

      .body {
        font-size: 16px;
        color: #000000;
        line-height: 30px;
        padding: 32px 20px 26px;
        box-shadow: 0 0 5px rgba(#7d4c21, 0.2);
        border-radius: 0 0 30px 30px;
      }

      &:not(:first-of-type) {
        .title {
          border-radius: 30px;
        }

        .body {
          display: none;
        }
      }
    }
  }

  .contact {
    padding: 55px 42px 75px;
    background-image: url("../images/contact.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 40px;
    overflow: hidden;

    @include mob480 {
      padding: 45px 15px 45px;
    }

    .head {
      text-align: center;

      * {
        color: #fff;

        &:after {
          background-color: #fff;
          margin-right: auto;
          margin-left: auto;
          margin-bottom: 40px;
        }
      }
    }

    form {
      .field {
        width: 100%;
        height: 50px;
        position: relative;

        &:not(:last-child) {
          margin-bottom: 8px;
        }

        input,
        textarea {
          width: 100%;
          height: 100%;
          border: 0;
          border-radius: 100px;
          padding-left: 50px;
          padding-right: 10px;
          color: #000000;
          font-size: 16px;

          &::placeholder {
            color: #000000;
            font-size: 16px;
            transition: 0.5s;
          }

          @include LTR {
            padding-right: 50px;
            padding-left: 10px;
          }

          &:focus {
            &::placeholder {
              color: rgba(#7d4c21, 0.3);
            }
          }
        }

        textarea {
          border-radius: 25px;
          padding-top: 12px;
          resize: none;
        }

        &:last-of-type {
          height: 90px;
          margin-bottom: 20px;
        }

        img {
          position: absolute;
          top: 16px;
          left: 20px;

          @include LTR {
            left: unset;
            right: 20px;
          }
        }
      }

      button {
        width: 100%;
        height: 50px;
        border-radius: 100px;
        background-color: #7d4c21;
        @extend .Flex;
        @extend .hover;
        font-size: 18px;
        color: #fff;
        border: 0;
        border: 1px solid transparent;

        &:hover {
          border-color: #fff;
          color: #fff;
        }
      }
    }
  }
}

